              <li><span><em class="red">*</em> </span> <input v-model.trim='formVC.repairService' maxlength="11" type="" name="" placeholder=""></li>
              <li><span><em class="red">*</em> </span><span class="select" @click='isShowtk'><span class="select-val">{{ formVC.accidentType }}</span><a></a></span></li>
              <li><span><em class="red">*</em> </span><span class="select" @click='isShowtv'><span class="select-val">{{ formVC.remark }}</span><a></a></span></li>
              <li v-show='isShowD'><span><em class="red">*</em> </span> <input v-model.trim='formVC.other' type="" name="" placeholder="" style="width: 70%"></li>
							<li><span class="iconfont icon-tianjiatupian1"><em class="red " style="visibility: hidden;">*</em>  <input class="input"  type="file" id="file" accept="image/*" mutiple="mutiple" /></span> 
