<template>
  <div>
	<div class="top"></div>

          <div class="confirm iconfont icon-tishi- display-none"> : "*",/HR</div>

     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div></div>
          		<a></a> 
          </div>
     </div>
     <!--   -->
      <div class="feedback-box" style="margin-top:1.25rem">
          <ul>
               <li><span><em class="red">* </em>:</span><span @click='isShowa=false' class="select" id="select" style="width: 60%"><span class="select-val">{{ formV.faultClass }}</span><a></a></span></li>
                <li><span><em class="red" style="visibility: hidden;">* </em>:</span><span id="faulttext">{{ formV.faultNo }}</span></li>
           </ul>
         </div>
        <ul class="feedback-box" style="margin-top:1.25rem">
          <li><span><em class="red">*</em> :</span><a><input type="" name="" v-model='formV.bwReader' style='border:1px solid #ddd;width:100%'></a></li>
          <li><span><em class="red">*</em> :</span><a><input type="" name="" v-model='formV.coReader' style='border:1px solid #ddd;width:100%'></a></li>
        </ul>

          <div class="feedback-box" style="margin-top:1.25rem">
          <h1></h1>
          <textarea placeholder="" maxlength="500" v-model="formV.mainTenanceFeedback"></textarea>
           <h1></h1>
          <textarea placeholder="" maxlength="500" v-model="formV.treatmentMeasure"></textarea>
         </div>

         <div class="feedback" :class="{'display-none': isShowa}">
               <div class="feedback-1">
               <ul v-for='(va,index) in faultClassV'>
                    <label><li @click='ac(index)'><label>{{ va }}</label><input type="radio" name="feedback"></li></label>                   
               </ul>
               </div>
          </div>

     <!--      <div class="feedback01 display-none">
               <div class="feedback-2">
               <ul>
                    <li><label>B01-</label><input type="radio" name="feedback" checked='ture'></li>
                    <li><label>B02-</label><input type="radio" name="feedback"></li>
                    <li><label>B03-(CD)</label><input type="radio" name="feedback"></li>
                    <li><label>B04-(FD)</label><input type="radio" name="feedback"></li>
                     <li><label>B09-</label><input type="radio" name="feedback"></li>
               </ul>
               </div>
          </div> -->
          <div class="footer-none"></div>
          <div class="bottom">
               <div v-on:click="completeOrder" class="complete-btn"></div>
          </div>

</div>
</template>
<script>
import{overOrderAccident,overOrderOther,faultClass} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'closeOrders02',   

	data () {
		return {
			faultClassV: ['','','','()','()','','','/','','//','(/)','','','//','',''],
				faultNoV: ['R01~R99','Z01','K01~K99','N01~N99','D01~D99','G01~G99','E01~E99','H01~H99','B01~B99','C01~C99','P01~P03','Q01~Q03','T01~T04','L01~L99','U01~U99','S01~S99'],
				isShowa: true,
				formV: {
					faultClass: '',
					faultNo: '',
					treatmentMeasure: '',
					maintenanceFeedback: '',
          bwReader: '',
					coReader: ''
				},
				items: ''
			}
		},
     methods:{
		 getData(){
			var that = this
			that.items = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		 },
      // 
		 completeOrder() {
			var that =this
			that.formV.woNumber = that.items.woNumber
      // console.log(that.formV)
			overOrderAccident(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path:'/competent/completeOrder'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			 
		     
		}, 
		ac(str){
			var that = this
			that.formV.faultClass = that.faultClassV[str]
			that.formV.faultNo = that.faultNoV[str]
// 			faultClass(that.formV)
// 			.then((res) => {
// 				if(res.data.code == 0){
// 					that.formV.faultNo = res.data.faultNo
// 				}
// 			})
			that.isShowa = true
		}
  }, 
   created(){   
	   this.getData()
  },


}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



