<template>
  <div>
     <div class="nav-fixed">
          <div class="nav">
             
          </div>
     </div>
     <div class="top"></div>


<!--  -->
		<div class="work-order">
			<div class="receipt">
				<div class="" id="etc"><router-link style='width:100%' to='/engineer/alreadyorder'></router-link></div>
				<div class="receipt-color" id=""><router-link style='width:100%; color: #7598ff' to='/engineer/examineOrder'></router-link></div>
				<div class="" id=""><router-link style='width:100%' to='/engineer/completeOrder'></router-link></div>
				<div class="" id=""><router-link style='width:100%' to='/engineer/catchSheet'></router-link></div>		
			</div>
		</div>	
      <!--    -->
             <div class="received">
                <a @click='details(list)' v-for='list in items'>
					<div> 
						   <div class="received-box" id="event">
							   <div class="received-time">
									<span>{{ list.repairTime}}</span>
							   </div>
							   <div class="received-box1">
									<div class="received-box2">
									<h1 class="iconfont icon-shebei1"><a>{{ list.woNumber}}</a></h1>
									<span :class='list.examine'>{{ list.woState}}</span>
									</div>
									<h2 class="received-name">:<span>{{ list.custName}}</span></h2>
									<h2 class="event-name-1">:<span>{{ list.faultType}}</span></h2>
									<div class="received-box3">
									<h1 class="received-name">{{ list.dd }}:<span>{{ list.describe}}</span></h1>
									<a>>></a>
									</div>
							   </div>
						  </div>
					</div>
				</a>

				
             </div>  
                
                <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666"></div>
		<!--  -->
  <div class="footer-none"></div>
  		  <footer class="footer1">
  		    <footer class="footer">
  		      <section  class="on">
  				<router-link class='this' :to='{path: "/engineer/index"}'>
  					<i class="iconfont icon-shouye"></i>
  					<div></div>
  				</router-link>      
  		      </section>
  			<section>
  				<router-link class='this' :to='{path: "/engineer/alreadyorder"}'>
  					<i  style='color:#7898ff' class="iconfont icon-gongdan"></i>
  					<div style='color:#7898ff'></div>				
  				</router-link>
  			</section>      
  			<section>
  				<router-link class='this' :to='{path: "/engineer/index03"}'>
  					<i class="iconfont icon-_shebeibaoyang"></i>
  					<div></div>
  				</router-link>      
  			</section>
  		    </footer>
  		  </footer>

</div>
</template>

<script>
import {enginnerOrder} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'ExamineOrder',  
	data () {
		return {
			items:'',
			showgd:false,
			dd: '',
			formV: {
				woStatus: '3'
			}
		}
	},
	mounted(){
		sessionStorage.removeItem('examineOrderV')
	},
	methods:{  
		getData(){
			var that = this
			enginnerOrder(that.formV)
			.then((res) => {
			if(res.data.data==''){
	            this.showgd=true;
	          }

				if(res.data.code == 0){
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
			if(res.data.data[i].faultType == ''){
              res.data.data[i].dd = ''
              // that.dd == ''
            }else{
              // alert(2)
              res.data.data[i].dd = ''
              // that.dd == ''
            }
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = ''
							}
						}
						 if(res.data.data[i].woState == ''){
               res.data.data[i].examine = 'examine1'
            }else if(res.data.data[i].woState == ''){
                res.data.data[i].examine = 'examine2'
            }else if(res.data.data[i].woState == ''){
                res.data.data[i].examine = 'examine'
            } 
					}
					that.items = res.data.data					
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('alreadyorderV',JSON.stringify(str))
			if(str.woState == ''){
				that.$router.push({path: '/engineer/adoptOrder'})
			}else if(str.woState == ''){
				that.$router.push({path: '/engineer/pendingOrders'})
			}else if(str.woState == ''){
				that.$router.push({path: '/engineer/reject'})
			}
			
		}
	}, 
	created(){     
		this.getData()
	},

}

</script>


<style scoped>

</style>




