<template>
	<div>
	<div class="top-order"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)"  class="iconfont icon-fanhui"></a>
          		<div></div>
          		<a></a> 
          </div>
     </div>

     <div class="details-table">
     	<div class="details-table-title">
     		<div class="company">{{formV.custName}}</div>			
     		<div class="job-number">:<span>{{formV.woNumber}}</span></div>

     	</div>
     	<div class="details-table-box">
     		<ul>
      		<li><span></span><span>{{formV.serviceArea}}</span></li>
      		<li><span></span><span>{{formV.repairMan}}</span></li>
      		<li><span></span><span>{{formV.repairPhone}}</span></li>
      		<li><span></span><span>{{formV.faultType}}</span></li>
      		<li><span></span><span>{{formV.accidentType}}</span></li>
      		<li><span></span><span id="number">{{formV.describe}}</span></li>
      	</ul>
     	</div>
     </div>
<!--  -->

<!-- <div class="category display-none">
 			<div class="category-order">
 				<h1></h1>
 				<div class="category-order-box">
 				<textarea class="textword" placeholder=""></textarea>
 				<div class="order-btn">
 					<span class="order-btn01"></span>
 					<span  class="order-btn02"></span>
 				</div>

 				</div>
 			</div>
 		</div> -->

 		<!--  -->

		<!--  -->
		<div class="acceptance" :class="{'display-none':isN}">
 			<div class="acceptance-order-ot">
 				<div class="call "> 
					<h1></h1>
					<div class="category-order-box">
						<div class="acceptance-order-btn">
							<span class="acceptance-order-btn01" @click="callCustSureNo()"></span>
							<a style="color: #7598ff" class="acceptance-order-btn02" :href="'tel:' + formV.repairPhone" @click="callCustSure()"></a>
						</div>
					</div>
 				</div>
 			</div>
 		</div>
		 <!--  -->
		<div class="acceptance" :class="{'display-none':isY}">
 			<div class="acceptance-order-ot">
 				<div class="problem"> 
					<h1></h1>
					<div class="category-order-box">
						<div class="acceptance-order-btn">
							<span  class="problem-order-btn01" @click="callCustSolveNo()"></span>
							<span  class="problem-order-btn02" @click="callCustSolve()"></span>
						</div>
					</div>
 				</div>
 			</div>
 		</div>

 		<div class="footer-none-two"></div>
          <div class="bottom">
               <a  @click="callCust">
				   <div class="complete-btn"></div>
			   </a>
               <div  class="solution-btn" @click="sceneSolveScan()"></div>
          </div>
          

</div>
</template>

<script>
import {phoneOrder,phoneOrderIsOver} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'CloseOrders02',    
	data () {
		return {
		  formV: '',
		  isN: true,
		  isY: true,
		}
	},
    methods:{
		getData(){
			var that = this
			that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
			if(that.formV.woState == ''){
				that.isY = false
			}
		},
		// 
		callCust(){
			var that = this;
			that.isN = false;
		  	that.isY = true;			
		},
		//
		callCustSure(){
			var that = this;
			that.isN = true;
		  	that.isY = false;
			phoneOrder(that.formV)
			.then((res) => {
				// console.log(res)
			})
		},
		//
		callCustSureNo(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
		},
		//
		callCustSolve(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
			that.formV.isOver = 1
			// console.log(that.formV)
			phoneOrderIsOver(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					if(that.formV.faultType == ''){		
						// alert(1)
						that.$router.push({path:'/competent/sceneSolveFeedback'})
					}else{				
						that.$router.push({path:'/competent/demandFeedback'})
					}					
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			
		},
		//
		callCustSolveNo(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
			that.formV.isOver = 0
			phoneOrderIsOver(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					if(that.formV.faultType == ''){						
						that.$router.push({path:'/competent/sceneSolveScan'})
					}else{							
						that.$router.push({path:'/competent/demandScan'})
					}
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			
		},
		sceneSolveScan(){
			var that = this;
			let woNumber = that.formV.woNumber;
			if(that.formV.faultType == ''){					
				that.$router.push({path:'/competent/sceneSolveScan'})
			}else{				
				that.$router.push({path:'/competent/demandScan'})
			}
			// this.$router.push({path:'/engineer/sceneSolveScan'})			
			// sessionStorage.setItem('workNum',woNumber)
		
		}
		
	}, 
   created(){    
	this.getData()
  },
}

</script>


<style scoped>

</style>



