<template>
  <div>
	<div class="top-order"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div></div>
          		<a></a> 
          </div>
     </div>

     <div class="details-table">
     	<div class="details-table-title">
     		<div class="company">{{ formV.custName }}</div>
     		<div class="job-number">:<span>{{ formV.woNumber }}</span></div>
     	</div>
     	  <div class="details-table-box">
        <ul>
          <li><span></span><span>{{formV.serviceArea}}</span></li>
          <li><span></span><span>{{formV.repairMan}}</span></li>
          <li><span></span><span>{{formV.repairPhone}}</span></li>
          <li><span></span><span>{{formV.faultType}}</span></li>
          <li><span></span><span>{{formV.accidentType}}</span></li>
          <li><span></span><span>{{formV.describe}}</span></li>
        </ul>
      </div>
     </div>

        <div class="completa-list">    
       <div class="decice-for-box">
               <span></span>
             </div>  
          <ul>
            <!--    <li><span>:</span><a>{{ formV.faultClass }}</a></li>
               <li><span>:</span><a>{{ formV.faultNo }}</a></li> 
                <li><span>:</span><a>{{ formV.describe }}</a></li>  -->
                 <li><span><i style="visibility: hidden;"></i>:</span><a>{{ formV.treatmentMeasure }}</a></li> 
          </ul>
     </div>

     <div class="acceptance" v-show="isShow">
      <div class="acceptance-order-ot">
        <div class="call"> 
        <div class="problem"> 
        <h1>?</h1>
        <div class="category-order-box">
        <div class="acceptance-order-btn">
          <span @click="toggle()" class="problem-order-btn01"></span>
          <span @click='competentindex' class="problem-order-btn02"></span>
        </div>
        </div>
        </div>

      </div>
    </div>
    </div>

     <div class="footer-none"></div>
       <div class="bottom">
               <div  @click="toggle()" class="complete-btn"></div>
          </div>

</div>
</template>

<script>
import {TechnicalCloseOrder} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'CloseOrderNo',
	data () {
		return {
				 isShow:false,
				 formV: ''
		}
	},
  methods:{  
		competentindex() {
			var that = this
			TechnicalCloseOrder(that.formV)
			.then((res) => {
				if(res.data.code == 0 ){
					that.$layerMsg(res.data.msg)
					that.$router.push({path:'/competent/orderIndex'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			
		},
		toggle:function(){
			this.isShow = !this.isShow;
		},
		getData(){
			var that = this
			that.formV = JSON.parse(sessionStorage.getItem('closeOrderListV'))
		}
  }, 
  created(){
    this.getData()
  },
}

</script>


<style scoped>

</style>



