<template>
  <div>
    
	<div class="top"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div>{{ formV.woNumber}}</div>
          	 	<p class="nav-comment" v-show="isShowbx" @click='evaluate()'></router-link></p> 
          </div>
     </div>
      <div class="state-text">
           <h1></h1>
      </div>
      <div class="state-text-box display-none">
           ,!
      </div>

     <div class="state"> 	
     	<div class="process">
     	<div class="proder-dian-box">
           	<div v-for='(item,index) in state'  class="process-dian"  :class="{'censhi': statevalue > index }">
           		{{item}}
           	</div>
     </div>
     	<div class="proder-dian-box" > 
           	<div  v-for='(item,index) in index' class="process-dian" >
           		<div class="dian" :class="{'censhi01': statevalue01 > index }"></div>
           		<div class="dian-01" :class="{'censhi02': statevalue02 > index }"></div>
           	</div>
     </div>
        		
 </div> 
<div class="processs-father">
	<div  class="processs-box">
        <div class="processs">
       <span>{{ formV.currentTime }}</span>
        <span>{{ statetext }}</span>
        </div><!-- <div class="demo-sj"></div> -->
    </div>
</div>


</div>
 
     <div class="completa-list">
     	<div class="satisfied display-none"></div>
     	
     	<h1></h1>
     	<ul>
     		<li><span>:</span><a>{{ formV.custName }}</a></li>
     		<li><span>:</span><a>{{ formV.department }}</a></li>
     		<li><span>:</span><a>{{ formV.enginnerName }}</a></li>
     		<li><span>:</span><a>{{ formV.enginnerPhone }}</a></li>
     		<li><span>:</span><a>{{ formV.faultType }}</a></li>
     		<li><span>:</span><a>{{ formV.accidentType }}</a></li>
     		<li><span>:</span><a>{{ formV.describe }}</a></li>
     	</ul>
     </div>

      <div class="completa-list completa-list-decice" v-show="isShowas">    
       <div class="decice-for-box">
               <span></span>
              <!--   <span class="decice-green"></span> -->
             </div>  
           <ul v-for='(va,index) in formV.partMessage'>
               <li><span>{{ index+1 }} ( ) :</span><a>{{ va }}<span></span></a></li> 
          </ul>
     </div>


  </div>
</template>

<script>

    export default {
      name: 'RepairStatusTo',   
    
      data () {

    return {
      isShowbx: false,
      isShowas: false,
      index: 6,
      formV: '',
      state: ['','','','','',''],
      statetext:{
       b:'',
       c:'',
       d:'',
       e:',',
       f:'',
       g:'',
       h:'',
      },
      // statevalue:[1,2,3,4,5,6],
      //  statevalue01:[1,2,3,4,5,6],
      //   statevalue02:[1,2,3,4,5,6]
    }
  },
     methods:{
      getIndex(index){  
         var statevalue=this.$route.query.name; 
        if(statevalue==''){
          this.statetext=this.statetext.b;
          this.statevalue=1;
          this.statevalue01=0;
          this.statevalue02=1;
        }
        if(statevalue==''){
          this.statevalue=2;
          this.statevalue01=1;
          this.statevalue02=2;
          this.statetext=this.statetext.c;
        }
        if(statevalue==''){
          this.statevalue=3;
          this.statevalue01=2;
          this.statevalue02=3;
          this.statetext=this.statetext.d;
        }
        if(statevalue==''){
          this.statevalue=4;
          this.statevalue01=3;
          this.statevalue02=5;
          this.statetext=this.statetext.e;
        }
        if(statevalue==''){
          this.statevalue=5;
          this.statevalue01=4;
          this.statevalue02=5;
          this.statetext=this.statetext.f;
        }
        if(statevalue==''){
          this.statevalue=6;
          this.statevalue01=6;
          this.statevalue02=6;
          this.statetext=this.statetext.h;
          this.isShowbx=true;
        }
        if(statevalue==''){
          this.state[4]="";
          this.statevalue=5;
          this.statevalue01=4;
          this.statevalue02=5;
          this.statetext=this.statetext.g;
          this.isShowas=true;
        }else{
           this.isShowas=false;

        }
        
},
      getData(){
          var that = this
          that.formV = JSON.parse(sessionStorage.getItem('repairIndexV'))
          // console.log(that.formV);
        },
     evaluate(){
      var that = this
          that.formV = JSON.parse(sessionStorage.getItem('repairIndexV'))
      var statevalue=that.formV.woNumber;
      this.$router.push({path:'/customer/evaluate',query:{name:statevalue}})
    }
    },
   created(){     
    this.getIndex();
    this.getData()  
    var statevalue=this.$route.query.name; 

  },



}

</script>


<style scoped>

</style>



