<template>
  <div>
	<div class="top"></div>

          <!-- <div class="confirm iconfont icon-tishi- display-none"> </div> -->

     <div class="nav-fixed">
          <div class="nav">
          	<router-link to='/engineer/closeOrder02' class="iconfont icon-fanhui"></router-link>
          		<div></div>
          		<a></a> 
          </div>
     </div>
    <!--   -->
     <div class="site-details">
           	<ul>
               <li><span>:</span><a>{{ formV.woNumber }}</a></li> 
               <li><span>:</span><a>{{ formV.machCode }}</a></li>
               <li><span>:</span><a>{{ formV.custName }}</a></li> 
               <li><span>:</span><a>{{ formV.location }}</a></li> 
               <li><span>:</span><a>{{ formV.repairMan }}</a></li>
               <li><span>:</span><a>{{ formV.repairPhone }}</a></li>
               <li><span>:</span><a>{{ formV.faultType }}</a></li>
               <li><span>:</span><a>{{ formV.describe }}</a></li> 
               <li><span>:</span><a>{{ formV.repairTime }}</a></li>
          </ul>
     </div>

          <div class="bottom" >
               <div v-on:click="sys_click()" class="upgrade-btn iconfont icon-saoyisao" id="sweep-code"> </div>
          </div>
       
</div>
</template>

<script>
  import wx from 'weixin-js-sdk'
	import {arriveOrderOther,arriveOrderAccident} from '../../../axiosConfig/axiosUtil'
    export default {
      name: 'closeOrders02',   
    
      data () {
        return {
            formV: {
                machCode: '',
                        // machCode: ''
                },
                isshow:false,
            }
        },
      mounted() {
       
      },
     methods:{  
      sys_click(){ 
        var that = this;
        var url=location.href;
          that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
          console.log(that.formV)
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // ,apialertpclogpc
                debug: false,
                // 
                appId: data.appId,
                // 
                timestamp: "" + data.timestamp,
                // 
                nonceStr: data.noncestr,
                // 
                signature: data.signature,
                // JSJS
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("" + data.errMsg);//wx.config
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
                wx.scanQRCode({
                    needResult: 1, // 01
                    scanType: ["qrCode"], // 
                    success: function (data) {
                        var result = data.resultStr; // needResult  1 
                        var ac = result.split('&')
                        var icc = 8
                        var acc = JSON.stringify(ac[1])                        
                        // alert(""+result);
                        var vals = acc.substring(68,acc.length-1)
                        if(vals==that.formV.machCode){
                          // alert(vals)
                          that.$router.push({path: '/engineer/sceneSolve02',query:{sbb: vals}})
                        }else{

                        that.$layerMsg('!')
                        }
                        // window.location.href = result;//
                    }
                });
 
        }); 
        



        },
			getData(){
				var that = this
				that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
                console.log(that.formV.machCode)
			}



    }, 
   created(){
    this.getData();
     var statevalue=this.$route.query.name;
  },


}

</script>

