<template>
<div id="app">
  <div class="nav-fixed">
          <div class="nav">
             
          </div>
     </div>
     <div class="top"></div>
  <!--  -->
  <div class="home">
  <!--  -->
    <div class="img">
    </div>
<!--  -->
    <div class="box">     
      <div class="repair_1">
        <div class="repair">        
          <section>
              <a @click="sys_click()">
             <i class="iconfont icon-weihubaoyang icon-shiguleixingtongji_1"></i>
          <div></div>
              </a>
          </section>

          <section>
             <a @click="sys_click02()">
          <i class="iconfont icon-saoyisao1 icon-shiguleixingtongji_2"></i>
               <div></div>
            </a>
          </section>      
        </div>
      </div>
    </div>

    <div class="competent-scheme">
      <div class="scheme-title">
          <div></div>
          <router-link to="/engineer/wholeOrder">>></router-link>
      </div>

          <a v-for="list in items" @click='details(list)'>
            <div class="competent-box receipt-mg" id="event">
               <div class="competent-box1">
                    <div class="competent-box2">
                    <h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>  
                    <span>{{ list.faultType }}</span>
                    </div>
                    <h2 class="competent-name">:<span>{{ list.custName }}</span></h2>
                     <h2 class="competent-name-1">:<span>{{ list.accidentType }}</span></h2>
										 <h2 class="competent-name-1">{{ list.dd }}:<span>{{ list.describe }}</span></h2>
                 <div class="competent-box3">
                    <h1 class="competent-name">:<span>{{ list.repairTime }}</span></h1>
                     <a>>></a>
                 </div>
               </div>
            </div>
          </a>
  <div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666;background: #fff" ></div>
      </div>

    <!--   -->
    <div class="scheme">
      <router-link to='/engineer/som' class="scheme-title">
          <div>SOM</div>
          <a>>></a>
      </router-link>
    </div>
    </div>

    <!--  -->
<div class="footer-none"></div>
		  <footer class="footer1">
		    <footer class="footer">
		      <section  class="on">
				<router-link class='this' :to='{path: "/engineer/index"}'>
					<i  style='color:#7898ff' class="iconfont icon-shouye"></i>
					<div style='color:#7898ff'></div>
				</router-link>      
		      </section>
			<section>
				<router-link class='this' :to='{path: "/engineer/alreadyorder"}'>
					<i class="iconfont icon-gongdan"></i>
					<div></div>				
				</router-link>
			</section>      
			<section>
				<router-link class='this' :to='{path: "/engineer/index03"}'>
					<i class="iconfont icon-_shebeibaoyang"></i>
					<div></div>
				</router-link>      
			</section>
		    </footer>
		  </footer>

         <!--  -->
  <!-- <router-view></router-view> -->
</div>
</template>

<script>
import wx from 'weixin-js-sdk'
import {enginnerOrder} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'Index',       
	data () {
    return {
			formV: {
				// openId: sessionStorage.getItem('openId'),
				woStatus: '1',
			},
			items: '',
      showgd:false,
      dd: ''
    }
  },
	mounted(){
		sessionStorage.removeItem('engineerIndexV')
	},
  methods:{  
      sys_click(){ 
        var that = this;
        var url=location.href;
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // ,apialertpclogpc
                debug: false,
                // 
                appId: data.appId,
                // 
                timestamp: "" + data.timestamp,
                // 
                nonceStr: data.noncestr,
                // 
                signature: data.signature,
                // JSJS
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("" + data.errMsg);//wx.config
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
			wx.scanQRCode({
				needResult: 1, // 01
				scanType: ["qrCode"], // 
				success: function (data) {
					var result = data.resultStr; // needResult  1 
					var ac = result.split('&')
					var icc = 8
					var acc = JSON.stringify(ac[1])
					// alert(""+result);
					var vals = acc.substring(68,acc.length-1)
					if(vals){
					  // alert(vals)
					  that.$router.push({path: '/engineer/scanMaintain',query:{sbb: vals}}),
					  location.reload()
					}else{
					  // alert(1)
					}
					// window.location.href = result;//
				}
			}); 
        }); 
        
        },

         sys_click02(){ 
        var that = this ;
         var url=location.href;
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // ,apialertpclogpc
                debug: false,
                // 
                appId: data.appId,
                // 
                timestamp: "" + data.timestamp,
                // 
                nonceStr: data.noncestr,
                // 
                signature: data.signature,
                // JSJS
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("" + data.errMsg);//wx.config
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
                wx.scanQRCode({
                    needResult: 1, // 01
                    scanType: ["qrCode"], // 
                    success: function (data) {
                        var result = data.resultStr; // needResult  1 
                        var ac = result.split('&')
                        var icc = 8
                        var acc = JSON.stringify(ac[1])                        
                        // alert(""+result);
                        var vals = acc.substring(68,acc.length-1)
                        if(vals){
                          // alert(vals)
                          that.$router.push({path: '/engineer/scan',query:{sbb: vals}}),
						  location.reload()
						  
                        }else{
                          // alert(1)
                        }
                        // window.location.href = result;//
                    }
                });
 
        }); 
        },
		getData(){
			var that = this
			enginnerOrder(that.formV)
			.then((res) => {
				if(res.data.code == 0){
          if(res.data.data==''){
              this.showgd=true;
            }else{
              this.showgd=false;
            }
					// res.data.data.repairTime = that.$date(res.data.data.repairTime)
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
            if(res.data.data[i].faultType == ''){
              res.data.data[i].dd = ''
              // that.dd == ''
            }else{
              // alert(2)
              res.data.data[i].dd = ''
              // that.dd == ''
            }
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = ''
							}
						}
					}
					that.items = res.data.data
				}
			})
		},
		details(str){
			var that = this
			sessionStorage.setItem('engineerIndexV',JSON.stringify(str))
			// console.log(str)
			if(str.faultType == ''){
				that.$router.push({path: '/engineer/workDetails'})
			}else{
				that.$router.push({path: '/engineer/demandClass02'})
			}
			
		}
  }, 
  created(){   
		this.getData()
  },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



