<template>
  <div>
	<div class="top"></div>

    <div class="confirm iconfont icon-tishi- display-none"> </div>

     <div class="nav-fixed">
          <div class="nav">
          	<a  @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div></div>
          		<a></a> 
          </div>
     </div>
    <!--   -->
     <div class="site-details">
     	   <ul>
               <li><span>:</span><a>{{ formV.woNumber }}</a></li> 
               <li><span>:</span><a>{{ formV.machCode }}</a></li> 
               <li><span>:</span><a>{{ formV.custName }}</a></li> 
			   <li><span>:</span><a>{{ formV.location }}</a></li> 
               <li><span>:</span><a>{{ formV.repairMan }}</a></li>
               <li><span>:</span><a>{{ formV.repairPhone }}</a></li>
               <li><span>:</span><a>{{ formV.faultType }}</a></li>
               <li><span>:</span><a>{{ formV.describe }}</a></li> 
               <li><span>:</span><a>{{ formV.repairTime }}</a></li>
          </ul>
     </div>

<!--  -->
  <div class="popup" v-if='isShowA'>
       <div class="popup-1">
         <h1></h1>
            <div class="popup-2" v-for='(vb,index) in items'>
                <input type="text" v-model='vb.model' name="" placeholder="">&nbsp&nbsp
                <input type="text" v-model='vb.number' name="" placeholder="">
                <span class="iconfont icon-shanchu" @click='removeNew(index)'></span>
            </div>
			
	        <div class="popup-3" @click='addNew()'>
				<span class="iconfont icon-tianjia"></span>
			</div>  
			<div class="popup-2">
			    <input type="text" name="" style='width:90%' v-model='formV.orderReason' placeholder="">
			</div>
			
			
	       <div class="scene-btn">
	            <span class="cancel" @click='isShowA=false'></span>
	            <span class="sub" @click='subMit()'></span>
	       </div>
       </div>
  </div>


          <!--  -->
          <div class="category" :class="{'display-none':isShow}">
               <div class="category-order">
                    <h1></h1>
                    <div class="category-order-box">
                    <textarea class="textword" v-model='turnOrderReson' placeholder=""></textarea>
                    <div class="order-btn">
                         <span class="order-btn01" @click='isShow=true'></span>
                         <span  class="order-btn02" @click='isTurnOrder()'></span>
                    </div>

                    </div>
               </div>
          </div>

			<div class="bottom">
				<a  v-on:click="sceneSolveFeedback"  class="sweep-btn"></a>
			   <div class="upgrade-btn" @click='isShowA=true'></div>
			   <div class="scenebtn-order-btn" @click='turnOrder()'></div>
			</div>
	

</div>
</template>

<script>
	
import {arriveOrderAccident} from '../../../axiosConfig/axiosUtil'
import {turnOrder,partsOrder} from '../../../axiosConfig/axiosUtil'	
export default {
    name: 'Reject',       
    data () {
		return {
		  newTxt: '',
		  formV: {
		  	woNumber: '',
		  	machCode: '',
			orderReason: ''
		  },
		  machCode: '',
		  turnOrderReson: '',
		  isShow: true,
		  isShowA: false,
		  toId: '2',
		  items: [
			  {id:1,model: '',number: ''},			
		  ],
		  
		}
	},
     methods:{
    sceneSolveFeedback: function () {
      this.$router.push({path:'/engineer/sceneSolveFeedback'})
    },
	// 
	isTurnOrder(){
		var that = this
		that.formV.turnOrderReson = that.turnOrderReson
		turnOrder(that.formV)
		.then((res) => {
			if(res.data.code == 0){
				that.$layerMsg(res.data.msg)
				that.$router.push({path: '/engineer/catchSheet'})
			}else{
				that.$layerMsg(res.data.msg)
			}
		})
	},
	subMit(){
		var that = this
		that.formV.partsTypeNumber = that.items
		partsOrder(that.formV)
		.then((res) => {
			if(res.data.code == 0){
				that.$layerMsg(res.data.msg)
				that.$router.push({path: '/engineer/examineOrder'})
			}else{
				that.$layerMsg(res.data.msg)
			}
		})
	},
	addNew(){
		var that = this		
		if(that.toId > 5){
			that.$layerMsg('5!')
			return false;
		}else{
			that.items.push({
				id: that.toId++,
				model: '',
				number: ''
			})
		}
	},
	removeNew(str){
		var that = this
		if(that.items.length > 1){			
			that.items.splice(str,1)
		}else{
			that.$layerMsg('0!')
			return false
		}
		
		
	},
	// 
	turnOrder(){
		var that = this
		that.isShow = false	
	},
	getData(){
		var that = this;
		that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		console.log(that.formV)
	  	var statevalue=this.$route.query.sbb;
	  	this.formV.woNumber=that.formV.woNumber
		this.formV.machCode=statevalue;
		console.log(this.formV.machCode)
		console.log(statevalue)
		if(that.formV.woState != ''){
			arriveOrderAccident(that.formV)
			.then((res) => { 
				console.log(res);
				console.log(this.machCode)
				if(res.data.code == 1){
					that.$layerMsg(res.data.msg);
				}else{ 
					this.isshowb=true;
				}

			})	
		}else{
			
		}
		
		that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
		console.log(that.formV.woNumber)
	}

  }, 
   created(){
    var statevalue=this.$route.query.sbb;
	this.getData()
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



