<script>
  import wx from 'weixin-js-sdk'
  import {getSign} from '../../../axiosConfig/axiosUtil'
    export default {
      name: 'Customerindex',    
      data () {
    return {
       formV: {
          http: '' ,
        },
      statevalue:true,
      id: '',
      result: '',

    }
  },
  
     methods: {              
      sys_click(){ 
        var that = this;
        var url=location.href;
        $.get("http://47.106.110.201:80/som/getSign?url="+url, function(data){    
            wx.config({
                // ,apialertpclogpc
                debug: false,
                // 
                appId: data.appId,
                // 
                timestamp: "" + data.timestamp,
                // 
                nonceStr: data.noncestr,
                // 
                signature: data.signature,
                // JSJS
                jsApiList: ['checkJsApi', 'scanQRCode']
            });

        });
        wx.error(function (data) {
            alert("" + data.errMsg);//wx.config
        });
 
        wx.ready(function () {
            wx.checkJsApi({
                jsApiList: ['scanQRCode'],
                success: function (data) {
                }
            });
                wx.scanQRCode({
                    needResult: 1, // 01
                    scanType: ["qrCode"], // 
                    success: function (data) {
						// alert(data)
                        var result = data.resultStr; // needResult  1 
                        var ac = result.split('&')
                        var icc = 8
                        var acc = JSON.stringify(ac[1])                        
                        // alert(""+result);
                        var vals = acc.substring(68,acc.length-1)
                        if(vals){
                          // alert(vals)
                          that.$router.push({path: '/customer/accidentScan',query:{sbb: vals}}),
						  location.reload()
                        }else{
                          // alert(1)
                        }
                        // window.location.href = result;//
                    }
                });
 
        }); 
        



        },
 
    
  cli(){
     if(this.statevalue==true){   
      this.statevalue=false;
    }else{  
      this.statevalue=true;
    }

  },

    customerindex: function () {
      this.$router.push({path:'/customer/index'})
    },
    repairIndex: function () {
      this.$router.push({path:'/customer/repairIndex'})
    },
    feedbackindex:function(){
    	this.$router.push({path:'/customer/feedbackindex'})
    },
    faultPlan:function(){
    	this.$router.push({path:'/customer/faultPlan'})
    },
    accident:function(){
    	this.$router.push({path:'/customer/accident'})
    },
    service:function(){
    	this.$router.push({path:'/customer/service'})
    }
  


  }, 
   created(){
        this.indexUrl = localStorage.getItem('openId');

  },


}

</script>


