<template>
	<div>
	<div class="top-order"></div>
     <div class="nav-fixed">
          <div class="nav">
          	<router-link class="iconfont icon-fanhui" to='/engineer/alreadyorder'></router-link>
          <!-- 	<a @click="$router.back(-1)" ></a> -->
          		<div></div>
          		<a></a> 
          </div>
     </div>

     <div class="details-table">
     	<div class="details-table-title">
     		<div class="company">{{formV.custName}}</div>			
     		<div class="job-number">:<span>{{formV.woNumber}}</span></div>

     	</div>
     	<div class="details-table-box">
     		<ul>
      		<li><span></span><span>{{ formV.machCode }}</span></li>
      		<li><span></span><span>{{ formV.devName }}</span></li>
      		<li><span></span><span>{{ formV.unitType }}</span></li>
      		<li><span></span><span>{{ formV.department }}</span></li>
      		<li><span></span><span>{{ formV.repairMan }}</span></li>
      		<li><span></span><span id="number">{{ formV.repairPhone }}</span></li>
      		<li><span></span><span>{{ formV.location }}</span></li>
      		<li><span></span><span>{{ formV.faultType }}</span></li>
      		<li><span></span><span>{{ formV.accidentType }}</span></li>
      		<!-- <li><span></span><span>{{ formV.faultNo }}</span></li> -->
      		<li><span></span><span>{{ formV.describe }}</span></li>
      		<li><span></span><span>{{ formV.repairTime }}</span></li>
      		<li><span></span><span class="faultImg">
					 	{{showNull}}
					 	<img v-for="(item,index) in picture" :key="index" :src="item.custName" @click="pictureBig(index)">
					</span></li>
      		<div class="order-img">
      		</div>
      	</ul>
     	</div>
     </div>
<!--  -->

<!-- <div class="category display-none">
 			<div class="category-order">
 				<h1></h1>
 				<div class="category-order-box">
 				<textarea class="textword" placeholder=""></textarea>
 				<div class="order-btn">
 					<span class="order-btn01"></span>
 					<span  class="order-btn02"></span>
 				</div>

 				</div>
 			</div>
 		</div> -->

 		<!--  -->

		<!--  -->
		<div class="acceptance" :class="{'display-none':isN}">
 			<div class="acceptance-order-ot">
 				<div class="call "> 
					<h1></h1>
					<div class="category-order-box" style="padding:inherit;">
						<div class="acceptance-order-btn">
							<span class="acceptance-order-btn01" @click="callCustSureNo()"></span>
							<a style="color: #7598ff" class="acceptance-order-btn02" :href="'tel:' + formV.repairPhone" @click="callCustSure()"></a>
						</div>
					</div>
 				</div>
 			</div>
 		</div>
		 <!--  -->
		<div class="acceptance" :class="{'display-none':isY}">
 			<div class="acceptance-order-ot">
 				<div class="problem"> 
					<h1></h1>
					<div class="category-order-box">
						<div class="acceptance-order-btn">
							<span  class="problem-order-btn01" @click="callCustSolveNo()"></span>
							<span  class="problem-order-btn02" @click="callCustSolve()"></span>
						</div>
					</div>
 				</div>
 			</div>
 		</div>

 		<div class="footer-none-two"></div>
          <div class="bottom">
               <a  @click="callCust" v-show="isA">
				   <div class="complete-btn"></div>
			   </a>
               <div  class="solution-btn" @click="sceneSolveScan()"></div>
          </div>
		<div class="picShade" v-if="isShade" @click="picShade()">
				<img :src="pictureB">
			</div>
</div>
</template>

<script>
import {phoneOrder,phoneOrderIsOver,woNumberPicture} from '../../../axiosConfig/axiosUtil'
export default {
	name: 'CloseOrders02',    
	data () {
		return {
		  formV: '',
		  isN: true,
		  isY: true,
		  isA: true,
		  picture: '',
		isShade: false,
		pictureB: '',
		showNull: '',
		}
	},
    methods:{
		getData(){
			var that = this
			that.formV = JSON.parse(sessionStorage.getItem('alreadyorderV'))
			if(that.formV.woState == ''){
				that.isY = false;	
			}
			if(that.formV.woState == ''){
				that.isA = false;	
			}
		},
		// 
		callCust(){
			var that = this;
			that.isN = false;
		  	that.isY = true;			
		},
		//
		callCustSure(){
			var that = this;
			that.isN = true;
		  	that.isY = false;
			phoneOrder(that.formV)
			.then((res) => {
				// console.log(res)
			})
		},
		//
		callCustSureNo(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
		},
		//
		callCustSolve(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
			that.formV.isOver = 1
			// console.log(that.formV)
			phoneOrderIsOver(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					if(that.formV.faultType == ''){		
						// alert(1)
						that.$router.push({path:'/engineer/sceneSolveFeedback'})
					}else{				
						that.$router.push({path:'/engineer/demandFeedback'})
					}					
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			
		},
		//
		callCustSolveNo(){
			var that = this;
			that.isN = true;
		  	that.isY = true;
			that.formV.isOver = 0
			phoneOrderIsOver(that.formV)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					if(that.formV.faultType == ''){						
						that.$router.push({path:'/engineer/sceneSolveScan'})
					}else{							
						that.$router.push({path:'/engineer/demandScan'})
					}
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
			
		},
		sceneSolveScan(){
			var that = this;
			let woNumber = that.formV.woNumber;
			if(that.formV.faultType == ''){					
				that.$router.push({path:'/engineer/sceneSolveScan'})
			}else{				
				that.$router.push({path:'/engineer/demandScan'})
			}
			// this.$router.push({path:'/engineer/sceneSolveScan'})			
			// sessionStorage.setItem('workNum',woNumber)
		
		},
		pictureBig(inx){
			var that = this
			
			that.isShade = true
			that.pictureB = that.picture[inx].custName
			// console.log(that.pictureB,"1111")
		},
		picShade(){
			this.isShade = false 
		}
		
	}, 
   created(){    
	this.getData()
	let picFault = {woNumber:this.formV.woNumber}
		woNumberPicture(picFault).then((res) => {
				if(res.data.msg == ''){
					this.showNull = ''
				}else{
					this.picture = res.data.data
				}
			})
  },
}

</script>

<style scoped>

</style>



