<template>
<!--  -->
  <div>
     <div class="nav-fixed">
		<div class="nav">
			
		</div>
     </div>
     <div class="top"></div>
<!--  -->
		<div class="receipt">
			<router-link  to='/competent/orderIndex'></router-link>
			<router-link class="receipt-color" to='/competent/alreadyOrder'></router-link>
			<router-link to='/competent/examineOrder'></router-link>
			<router-link to='/competent/completeOrder'></router-link>
		</div>
		<!--          --> 
		<div @click='details(list)' v-for='list in items' class="received-box">
			<div class="received-time">
				<span>{{ list.repairTime }}</span>
			</div>
			<div class="received-box1" id="received-orders">
				<div class="received-box2">
				<h1 class="iconfont icon-shebei1"><a>{{ list.woNumber }}</a></h1>
				<span>{{ list.woState }}</span>
				</div>
				<h2 class="received-name">:<span>{{ list.custName }}</span></h2>
				<h2 class="event-name-1">:<span>{{ list.faultType }}</span></h2>
				<div class="received-box3">
				<h1 class="received-name">:<span>{{ list.describe }}</span></h1>
				<a>>></a>
				</div>
			</div>
		</div>
		<div v-show='showgd' style="text-align: center; font-size: 2.5rem;padding: 1.5rem 0;color: #666666"></div>

		<!--  -->
        <div class="footer-none"></div>
	 	<footer class="footer1">
			<footer class="footer">
				<section class="on">
					<router-link to="/competent/index">
						<i class="iconfont icon-shouye"></i>
						<div></div>
					</router-link>			    
				</section>
				<section>
					<router-link to="/competent/orderIndex">
						<i  style='color:#7898ff' class="iconfont icon-gongdan1"></i>
						<div style='color:#7898ff'></div>
					</router-link>
				
				</section>
				<section>
					<router-link to="/competent/maintainIndex">
						<i class="iconfont icon-_shebeibaoyang"></i>
						<div></div>
					</router-link>    	
				</section>
				<section>
					<router-link to="/competent/dutyState">
						<i class="iconfont icon-yuangongguanli"></i>
						<div></div>
					</router-link>			
				</section>
			</footer>
		</footer>

 </div>
</template>

<script>
import {TechnicalNotOverOrder} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'OrderIndex',   
    data () {
		return {
			formV: {
				woStatus : '4'
			},
			items: '',
      showgd: '',
		}
	},
    methods:{
		getData(){
			var that = this
			TechnicalNotOverOrder(that.formV)
			.then((res) => {
         if(res.data.data==''){
              this.showgd=true;
            }
				if(res.data.code == 0){
					for(var i = 0,l = res.data.data.length;i<l;i++){
						res.data.data[i].repairTime = that.$date(res.data.data[i].repairTime)
						for(var v in res.data.data[i]){
							if(!res.data.data[i][v]){
								res.data.data[i][v] = ''
							}
						}
					}					
					that.items = res.data.data					
				}
			})
		},
		details(str){
			var that = this
			// console.log(str)
			sessionStorage.setItem('alreadyorderV',JSON.stringify(str))
			if(str.woState == ''){ 
				if(str.faultType == ''){       
					that.$router.push({path:'/competent/sceneSolve02'})
				}else{
					that.$router.push({path:'/competent/demandScan'})
				}
			}else if(str.woState == ''){
				if(str.faultType == ''){
					that.$router.push({path:'/competent/sceneSolveFeedback'})	
				}else{
					that.$router.push({path:'/competent/demandFeedback'})	
				}
							
			}else if(str.woState == ''){
				if(str.faultType == ''){
					that.$router.push({path:'/competent/sceneSolveScan'})	
				}else{
					that.$router.push({path:'/competent/demandScan'})	
				}
			}else if(str.woState == ''){
				if(str.faultType == ''){       
					that.$router.push({path:'/competent/sceneSolveFeedback'})
				}else{
					that.$router.push({path:'/competent/demandFeedback'})
				}	
			}else if(str.woState == ''){
				that.$router.push({path:"/competent/closeOrder02"})	
			}else if(str.woState == ''){
				// if(str.faultType == ''){
					that.$router.push({path: "/competent/sceneSolve02"})
				// }
			}else{
				that.$router.push({path:"/competent/closeOrder02"})
			}
			// else if(str.faultType != ''){
			// 	that.$router.push({path:"/competent/demand"})
			// }else if(str.woState == ''){
			// 	that.$router.push({path: "/competent/sceneSolve02"})
			// }else{
			// 	that.$router.push({path:"/competent/closeOrder02"})				
			// }
		}
	}, 
	created(){
		this.getData()
	},

}

</script>


<style scoped>

</style>




