<template>
  <div>
  
	<div class="top"></div>

          <div class="confirm iconfont icon-tishi- display-none"> </div>

     <div class="nav-fixed">
          <div class="nav">
          	<a @click="$router.back(-1)" class="iconfont icon-fanhui"></a>
          		<div></div>
          		<a></a> 
          </div>
     </div>
    <!--   -->
     <div class="site-details">
     	<ul>
               <li><span>:</span><a>{{ formV.machCode }}</a></li> 
               <li><span>:</span><a>{{ formV.devName }}</a></li> 
               <li><span>:</span><a>{{ formV.unitType }}</a></li>
               <li><span>:</span><a>{{ formV.location }}</a></li>
               <li><span>:</span><a>{{ formV.custName }}</a></li>
               <li><span>:</span><a>{{ formV.department }}</a></li>
               <li><span>:</span><a>{{ formV.repairMan }}</a></li>
               <li><span>:</span><a>{{ formV.repairPhone }}</a></li>
               <li><span>:</span><a>{{ formV.faultType }}</a></li>
               <li><span>:</span><a>{{ formV.accidentType }}</a></li>
			   <li><span>:</span><a>{{ formV.bwReader }}</a></li>
			   <li><span>:</span><a>{{ formV.coReader }}</a></li>
               <li><span>:</span><a>{{ formV.describe }}</a></li>
               <li><span>:</span><a>{{ formV.enginnerName }}</a></li>
               <li><span>:</span><a>{{ formV.enginnerPhone }}</a></li>
          </ul>
     </div>

        <div class="completa-list">      
          <h1></h1>
          <ul v-for="(vaa,index) in formV.partMessage">
               <li><span>{{ index+1 }} ( ) :</span><a>{{ vaa }} </a></li>
               <!-- <li><span>2 ( ) :</span><a>BBB 5</a></li> -->
          </ul>
		  <ul>
		  	<li><span>:</span><a>{{ formV.orderReason }}</a></li> 
		  </ul>
     </div>


               <!-- < -->
          <div class="category" v-show="isShow">
               <div class="category-order">
                    <h1></h1>
                    <div class="category-order-box">
                    <textarea class="textword" v-model='form.content' placeholder=""></textarea>
                    <div class="order-btn">
                         <span v-on:click="toggle()" class="order-btn01"></span>
                         <span class="order-btn02"> <a @click='TechnicalRejectedOrder()' style="color: #fff"></a></span>
                    </div>

                    </div>
               </div>
          </div>

          <div class="footer-none-two"></div>
          <div class="bottom">
               <div @click='TechnicalPartOrder()' class="adopt-btn"></div>
               <div @click="toggle()" class="rejected-btn"></div>
          </div>


</div>
</template>

<script>
import {TechnicalPartOrder,TechnicalRejectedOrder} from '../../../axiosConfig/axiosUtil'	
export default {
	name: 'PendingOrders',       
	data () {
		return {
		  isShow: false,
		  formV: '',
             va: ['','','','','',''],
		  form: {
			  woNumber: '',
			  content: ''
		  },
		}
	},
    methods:{
		toggle:function(){
			this.isShow = !this.isShow;
		},
		getData(){
		  var that = this
		  that.formV = JSON.parse(sessionStorage.getItem('examineOrderV'))
		//   console.log(that.formV)
		},
		TechnicalPartOrder(){
			var that = this
			that.form.woNumber = that.formV.woNumber
			TechnicalPartOrder(that.form)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path: '/competent/examineOrder'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		},
		TechnicalRejectedOrder(){
			var that = this
			that.form.woNumber = that.formV.woNumber
			TechnicalRejectedOrder(that.form)
			.then((res) => {
				if(res.data.code == 0){
					that.$layerMsg(res.data.msg)
					that.$router.push({path: '/competent/examineOrder'})
				}else{
					that.$layerMsg(res.data.msg)
				}
			})
		}

	}, 
	created(){      
		this.getData()
          $(function(){
               $('input,textarea').on('blur',function(){
                     window.scroll(0,0);
                     });
               $('select').on('change',function(){
                     window.scroll(0,0);
                     });
              })
	},


}

</script>


<style scoped>

</style>



